


html,body{ margin:0; padding:0; height:100%; width:100%; background-color:black;   text-align: center; }

#container{
  background-color: black;
}

#background{

  height:100%;
  width:100%;

  background-blend-mode: darken;
}

#header {

  font-size: 3.5rem;
    font-weight: 400;
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
    line-height: 1.30357em;
    margin-left: -.04em;
    letter-spacing: -.02em;
    margin: 0;
    display: block;

color:white;
  position:relative;
  width:100%;
  height:150px;
  background-color: black;
  text-shadow: 0 0 45px #ffffff;


}
 
.countdown {
color: white;
    letter-spacing: -1px;
    background: rgb(0, 0, 0);
    background: rgba(0, 0, 0, 0.7);
    padding: 10px;
    opacity: 0.75;
    bottom: 2%;
    left: 33%;
    width: 66%;
    max-width: 500px;
  position:fixed;
}








